<template>
  <overflow-image-slide v-bind:image="content.image" v-bind:video="content.video" v-bind:align="content.align" v-bind:active="active">
    <h2 class="upcase">{{title}}</h2>
    <p v-for="paragraph in content.paragraphs" v-bind:key="paragraph.index">{{paragraph}}</p>
  </overflow-image-slide>
</template>

<script>
  import overflow_image_slide from '../../components/organisms/overflow_image_slide'

  import 'nodelist-foreach-polyfill'; // ie11
  
  export default {
    props: ['title', 'content', 'active'],
    components: {
      'overflow-image-slide': overflow_image_slide
    }
  }
</script>


