<template>

  <!-- image left -->
  <div v-if="align === 'left'" class="row max-height-vertical box-overflow">
    
    <!-- media -->
    <div class="span-8-lg relative ind-2 content-translate" v-bind:class="mediaActiveClass">
      <div v-if="image" class="overflow-image" ref="overflowCard" :style="ratio_styles">
        <image-component v-bind:image="image"></image-component>
      </div>
      <div v-else-if="video" class="overflow-image" ref="overflowCard" :style="ratio_styles">
        <video-component v-bind:video="video"></video-component>
      </div>
    </div>

    <!-- content -->
    <div class="span-8-lg bg-white relative ind-1 border-left-right-blue">
      
      <div class="scroll-outer">
        <div class="slide-content relative ind-1 content-translate" v-bind:class="contentActiveClass">
          <slot></slot>
        </div>
        <span-background></span-background>
      </div>

      <!-- <crosshair-background></crosshair-background> -->
    </div>

  </div>

  <!-- image right -->
  <div v-else class="row max-height-vertical box-overflow">

    <!-- content -->
    <div class="span-8-lg bg-white relative ind-1 border-left-right-blue">

      <div class="scroll-outer">
        <div class="slide-content relative ind-1 content-translate" v-bind:class="contentActiveClass">
          <slot></slot>
        </div>
        <span-background></span-background>
      </div>

      <!-- <crosshair-background></crosshair-background> -->
    </div>

    <!-- media -->
    <div class="span-8-lg relative ind-2 content-translate" v-bind:class="mediaActiveClass">
      <div v-if="image" class="overflow-image overflow-left" ref="overflowCard" :style="ratio_styles">
        <image-component v-bind:image="image"></image-component>
      </div>
      <div v-if="video" class="overflow-image overflow-left" ref="overflowCard" :style="ratio_styles">
        <video-component v-bind:video="video"></video-component>
      </div>
    </div>
    
  </div>
  
</template>

<script>
  import image_component from '../atoms/image_component'
  import video_component from '../atoms/video_component'
  import span_background from '../atoms/span_background'
  import crosshair_background from '../atoms/crosshair_background'

  import {ratio_height, ratio_styles} from '../../utils/ratio_height'

  export default {
    props: ['image', 'video', 'align', 'active'],
    components: {
      'image-component': image_component,
      'video-component': video_component,
      'crosshair-background': crosshair_background,
      'span-background': span_background
    },
    data () {
      return {
        ratio_styles,
        contentActiveClass: 'content-inactive',
        mediaActiveClass: 'content-inactive'
      }
    },
    methods: {
      ratioInit () {
        ratio_height(this, this.$refs.overflowCard, 2/3)
        window.addEventListener('resize', () => {
          setTimeout(() => {
            ratio_height(this, this.$refs.overflowCard, 2/3)
          }, 500)
        })
      },
      activeCallback () {
        let int = 800
        if(this.active) {
          setTimeout(() => {
            this.ratioInit()
            this.contentActiveClass = ''
          }, int)
          setTimeout(() => {
            this.mediaActiveClass = ''
          }, int * 1.25)
        } else {
          setTimeout(() => {
            this.contentActiveClass = 'content-inactive'
          }, int)
          setTimeout(() => {
            this.mediaActiveClass = 'content-inactive'
          }, int * 1.25)
        }
      }
    },
    watch: {
      active () {
        this.activeCallback()
      }
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';

  // vertical align without flexbox
  // see http://zerosixthree.se/vertical-align-anything-with-just-3-lines-of-css/

  .overflow-image {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
    padding-right: calc(100%/8);
    // top: 50%;
    // transform: translateY(-50%);
    @media screen and (max-width: $md-max) {
      position: relative;
      box-sizing: border-box;
      padding-right: 0px;
      padding-left: 0px;
      margin-bottom: 72px;
    }
    @media screen and (max-width: $sm-max) {
      margin-bottom: 0px;
    }
    &.overflow-left {
      padding-right: 0px;
      width: calc(100% * 1.125);
      margin-left: calc(-100%/8);
      @media screen and (max-width: $md-max) {
        width: 100%;
        margin-left: 0px;
        margin-top: 72px;
      }
      @media screen and (max-width: $sm-max) {
        margin-top: 0px;
      }
    }
  }

  .slide-outer {
    display: flex;
  }


  .slide-content {
    position: relative;
    margin: auto;
    padding-top: 24px;
    padding-bottom: 24px;
    max-width: calc((100% * 6/8) - 48px); // 48px to account for margin
    &.extra-max-width {
      @media screen and (max-width: $md-max) {
        max-width: calc(100% - 24px - 24px);
      }
    }
  }

</style>



