<template>
    <div class="row scroll-outer">
      <div class="span-16-lg relative center-flex border-left-right-blue">
        
        <div class="relative ind-1 full-span-outer ">
          <div class="full-span-inner content-translate" v-bind:class="contentClass">
            <slot></slot>
          </div>
        </div>
        
        <!-- <crosshair-background></crosshair-background> -->
        <span-background v-if="!nobg" full="true"></span-background>

      </div>
    </div>
</template>

<script>
  import crosshair_background from '../atoms/crosshair_background'
  import span_background from '../atoms/span_background'

  export default {
    props: ['active', 'nobg'],
    components: {
      'span-background': span_background,
      'crosshair-background': crosshair_background
    },
    data () {
      return {
        contentClass: 'content-inactive'
      }
    },
    methods: {
      activeCallback () {
        let int = 800
        if (this.active) {
          setTimeout(() => {
            this.contentClass = ''
          }, int)
        } else {
          setTimeout(() => {
            this.contentClass = 'content-inactive'
          }, int)
        }
      }
    },
    mounted () {
      this.activeCallback()
    },
    watch: {
      active () {
        this.activeCallback()
      }
    }
  }
</script>


<style scoped lang="scss">

  @import '../../styles/variables';

  .full-span-outer {
    position: relative;
    box-sizing: border-box;
    display: flex;
    height: 100%; // 100vh
    max-height: 100%;
    width: 100%;
    @media screen and (max-width: $md-max){
      height: auto;
      padding-top: 48px;
      padding-bottom: 48px;
      min-height: calc(100vh - 96px - 96px)
    }
  }

  .full-span-inner {
    position: relative;
    margin: auto;
    max-width: 100% * 5/8;
    @media screen and (max-width: $sm-max) {
      max-width: calc(100% - 48px - 48px - 12px); // additional 12 overall added to make less cramped
    }
  }

</style>



