<template>
  <div v-bind:class="isSurvey">

    <div class="gutter-horizontal" :class="fullTemplate.gutter">

      <div v-for="(page, index) in pages" v-bind:key="index" class="slide" v-bind:class="[slide_active(index).class, slide_in_queue(index)]">

        <component v-bind:is="page.template" v-bind:content="page.content" v-bind:title="page.title" v-bind:active="slide_active(index).bool">
          {{page.content}}
        </component>

      </div>

    </div>
    
    <progress-bar :complete="percentComplete" :hidden="fullTemplate.progressHidden"></progress-bar>

    <navbar :nextCondition="nextCondition" ></navbar>
    
    <notification v-bind:state="notification_state" v-bind:captionState="caption_state"></notification>

  </div>
</template>

<style scoped lang="scss">
  @import '../styles/variables';

  .slide {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    transition: transform 0.9s ease-in-out, opacity 1.8s ease-in-out;
    &.page-inactive {
      transform: translateX(-200%);
      opacity: 0;
      @media screen and (max-width: $md-max) {
        position: fixed;
        width: calc(100% - 80px - 80px);
        margin-left: 80px;
      }
      @media screen and (max-width: $sm-max) {
        width: 100%;
      }
    }
    @media screen and (max-width: $md-max) {
      position: relative;
      height: auto;
      width: auto;
    }
  }

  .pdf-link {
    position: absolute;
    bottom: 0;
    left: 0;
    background: $debug;
    text-align: center;
    z-index: 99 !important;
  }
</style>

<script>
  import navbar from '../components/organisms/navbar'
  import progressBar from '../components/organisms/progressBar'
  import notification from '../components/organisms/notification'
  import pages from './pages'

  import contentful_init from '../contentful'

  // todo: set nextcondition to pass a function to store, which can then handle all conditoinal logic/return and disable nav if something is off.

  export default {
    data () {
      return {
        isSurvey: '',
      }
    },
    computed: {
      pages () {
        return this.$store.state.pages
      },
      notification_state () {
        return this.$store.state.notification_state
      },
      caption_state  () {
        return this.$store.state.checkbox
      },
      nextCondition () {
        return this.$store.state.nextCondition
      },
      percentComplete () {
        let curr = this.$store.state.page
        let len = this.$store.state.pages.length
        return curr/len
      },
      fullTemplate () {

        let defaultClasses = {
          gutter: '',
          progressHidden: false
        }
        
        if (!this.$store.state.pages[this.$store.state.page]) return defaultClasses

        const fullTemplates = ['pre-survey', 'post-survey']
        let currentTemplate = this.$store.state.pages[this.$store.state.page].template

        for (let i = 0; i < fullTemplates.length; i++) {
          if (fullTemplates[i].indexOf(currentTemplate) !== -1) {
            return {
              gutter: 'gutter-full',
              progressHidden: true
            }
          }
        }

        return defaultClasses

      }
    },
    methods: {
      slide_active (page) {
        let current_page = this.$store.state.page
        if (current_page !== page) {
          return {
            class: 'page-inactive',
            bool: false
          }
        } else {
          return {
            class: '',
            bool: true
          }
        }
      },
      slide_in_queue (this_page) {

        // set to display: none if not directly before or after slide ( prevent crashes )
        let current_page = this.$store.state.page

        if (this_page < current_page - 2 || this_page > current_page + 2) {
          return 'hide-all'
        } else {
          return ''
        }
        
      }
    },
    components: {
      navbar,
      notification,
      'progress-bar': progressBar,
      ...pages
    },
    mounted () {
      // get pages
      contentful_init(this, 'set_pages')
    }
  }
</script>



